.header__link:hover {
  font-size: 12px;
  color: #dfc691;
  text-shadow: -1px 0 1px #fff, 0 -1px 1px #fff, 0 1px 1px #fff, 1px 0 1px #fff, 0 0 8px #fff, 0 0 8px #fff, 0 0 8px #fff, 2px 2px 3px #000;
}
.hero__button:hover {
  background: rgba(1,1,1,0.5);
}
.pancakes__slider-before:hover,
.pancakes__slider-after:hover {
  box-shadow: 0 0 10px 1px #999;
  transform: scale(1.1);
  transform-origin: 50% 400%;
}
.pancakes__slider::selection,
.pancakes__icon::selection {
  background: transparent;
}
.pancakes-hide-content {
  opacity: 0;
}
.breakfast__switch:hover {
  box-shadow: 0px 0px 20px #aaa;
  transform: scale(1.2);
}
.js__book {
  transition: all 1s ease;
}
